html {
  --accent: #DB504A;
  /* accent     */
  --accent-dark: #4e3e51;
  /* accent 2   */
  --main: #326273;
  /* main color */
  --main-dim: #879dab;
  /* dimmed version of main color */
  --text: #2B323B;
  --grey1: #5F5E58;
  --grey2: #8D897C;
  --grey3: #B4B3A7;
  --grey4: #DAD7D2;
  --grey5: #F0EFED;
  --background: #ffffff;
  --shadow: rgba(32, 30, 30, .3);
  --github: #333;
  --font1: "Lora", charter, Georgia, Cambria, "Times New Roman", Times, serif;
  /* body text */
  --font2: "Source Sans Pro", "Helvetica Neue", Helvetica, Arial, sans-serif;
  /* headers and titles   */
  --font1-light: 400;
  --font1-regular: 400;
  --font1-bold: 600;
  --font2-light: 200;
  --font2-regular: 400;
  --font2-bold: 700;
}

html[data-theme=dark] {
  --accent: #d14c47;
  /* accent     */
  --accent-dark: #DBBEA1;
  /* accent 2   */
  --main: #5F8081;
  /* main color */
  --main-dim: #333D47;
  /* dimmed version of main color */
  --text: #CBCCCD;
  --grey1: #B1B1AE;
  --grey2: #959692;
  --grey3: #7C7E7B;
  --grey4: #636564;
  --grey5: #3B3D40;
  --background: #2B323B;
  --shadow: rgba(0, 0, 0, .3);
  --github: #000;
}

/* = = = = = = = = = = Main Styles = = = = = = = = = = */
*,
*::after,
*::before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  overflow: visible;
}

html {
  line-height: 1.75;
  letter-spacing: 0.002em;
  font-size: 1.275rem;
}

body {
  font-family: var(--font1);
  color: var(--text);
  margin: 0;
  background-color: var(--background);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-overflow-scrolling: touch;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font2);
  color: var(--accent-dark);
}

h1 {
  font-size: 4rem;
  font-weight: var(--font2-regular);
  line-height: 1;
}

img {
  height: auto;
  max-width: 100%;
}

a {
  color: var(--text);
  -webkit-transition: all 1s;
  transition: all 1s;
  -webkit-transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
  transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
}

a:hover {
  color: var(--accent);
}

table {
  border-left: 6px solid var(--accent);
}

table td,
table th {
  border: 0;
  padding: 12px;
  text-align: left;
  background-color: var(--grey5);
}

blockquote {
  border-left: 10px solid var(--grey4);
  padding-left: 1em;
  margin-left: 2.5em;
  font-style: italic;
  color: var(--accent-dark);
  background: var(--grey5);
  margin-right: 0;
  font-weight: 100;
}

pre {
  padding: 16px 32px;
  overflow-x: auto;
  font-family: monospace;
  font-size: 0.75rem;
}

pre code {
  border: 0;
  padding-right: 0;
  padding-left: 0;
}

code {
  font-family: monospace;
  font-size: 0.75rem;
}

svg {
  vertical-align: middle;
}

.hidden {
  display: none;
}

.flex-container {
  will-change: opacity;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  min-height: 100vh;
  -webkit-transition: -webkit-transform 0.6s cubic-bezier(0.16, 0.68, 0.43, 0.99);
  transition: -webkit-transform 0.6s cubic-bezier(0.16, 0.68, 0.43, 0.99);
  transition: transform 0.6s cubic-bezier(0.16, 0.68, 0.43, 0.99);
  transition: transform 0.6s cubic-bezier(0.16, 0.68, 0.43, 0.99), -webkit-transform 0.6s cubic-bezier(0.16, 0.68, 0.43, 0.99);
}

.flex-container.opaque {
  -webkit-transform: translate3d(300px, 0, 0);
  transform: translate3d(300px, 0, 0);
}

.flex-container::after {
  will-change: opacity;
  -webkit-transition: -webkit-opacity 0.6s cubic-bezier(0.16, 0.68, 0.43, 0.99);
  transition: -webkit-opacity 0.6s cubic-bezier(0.16, 0.68, 0.43, 0.99);
  transition: opacity 0.6s cubic-bezier(0.16, 0.68, 0.43, 0.99);
  transition: opacity 0.6s cubic-bezier(0.16, 0.68, 0.43, 0.99), -webkit-opacity 0.6s cubic-bezier(0.16, 0.68, 0.43, 0.99);
}

.flex-container.active.opaque::after {
  opacity: 0.5;
}

.flex-container.active.transparent::after {
  opacity: 0;
}

.flex-container.active::after {
  content: "";
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  background-color: rgb(0, 0, 0);
  opacity: 0.5;
}

.content {
  -webkit-box-flex: 1;
  -webkit-flex-grow: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
}

.wrapper {
  width: 960px;
  max-width: 90%;
  margin: 0.75em auto 0.75em;
  position: relative;
}

::-moz-selection {
  background: rgba(49, 30, 62, 0.7);
  color: white;
}

::selection {
  background: rgba(49, 30, 62, 0.7);
  color: white;
}

.clearfix::before,
.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

.clearfix::before {
  clear: both;
}

/* = = = = = = = = = = Header = = = = = = = = = = */
.main-header {
  font-family: var(--font2), sans-serif;
  padding: 15px 0;
  background-color: var(--background);
  -webkit-box-shadow: 0px -5px 15px var(--shadow);
  box-shadow: 0px -5px 15px var(--shadow);
}
.main-header .header-flex {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}
.main-header .logo {
  width: 80%;
  position: relative;
  text-align: center;
  color: var(--main);
}
.main-header .logo a {
  text-decoration: none;
  text-transform: uppercase;
  font-size: 27px;
  font-weight: var(--font2-light);
  color: var(--accent-dark);
}

.main-nav {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 300px;
  height: 100%;
  background-color: var(--background);
  padding: 25px 30px;
  -webkit-transform: translate3d(-300px, 0, 0);
  transform: translate3d(-300px, 0, 0);
}
.main-nav ul {
  padding: 15px 0 0;
  margin: 0;
  list-style: none;
}
.main-nav ul li {
  border-bottom: 1px solid var(--grey5);
}
.main-nav ul li a {
  display: block;
  padding: 15px 0;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: var(--font2-regular);
  font-size: 18px;
  color: var(--main);
}
.main-nav ul li a:hover {
  opacity: 0.8;
  color: var(--accent-dark);
}

.menu-icon-container {
  width: 25%;
  text-align: left;
}

.search-icon-container {
  width: 25%;
  text-align: right;
}

svg.menu-icon-house {
  width: 19px;
}

.menu-icon,
.menu-icon-close,
.search-icon a,
.search-icon-close {
  cursor: pointer;
  font-size: 18px;
  color: var(--main);
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  padding: 10px;
}
.menu-icon:hover,
.menu-icon-close:hover,
.search-icon a:hover,
.search-icon-close:hover {
  opacity: 0.8;
}

.left {
  text-align: left;
  float: left;
}

.right {
  text-align: right;
}

/* = = = = = = = = = = Theme toggle = = = = = = = = = = */
.theme-toggle {
  position: relative;
  width: 3.75rem;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 10px;
  transition: 0.3s cubic-bezier(0.4, 0.03, 0, 1);
  /* Toggle */
}
.theme-toggle label {
  display: block;
  background-color: rgba(120, 120, 120, 0.15);
  cursor: pointer;
}
.theme-toggle label,
.theme-toggle .toggle {
  border-radius: 100px;
}
.theme-toggle .toggle {
  position: absolute;
  width: 50%;
  height: 100%;
  background-color: var(--background);
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.08);
  transition: transform 0.2s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.theme-toggle .names {
  font-size: 0.4em;
  width: 76%;
  margin-left: 12%;
  position: relative;
  display: flex;
  justify-content: space-between;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.theme-toggle .dark {
  opacity: 0.5;
}
.theme-toggle p {
  color: var(--grey3);
}
.theme-toggle [type=checkbox] {
  display: none;
}
.theme-toggle [type=checkbox]:checked ~ label .toggle {
  transform: translateX(100%);
  background-color: var(--background);
  opacity: 0.75;
}
.theme-toggle [type=checkbox]:checked ~ label .dark {
  opacity: 0.75;
}
.theme-toggle [type=checkbox]:checked ~ label .light {
  opacity: 0.5;
}

@media (max-width: 768px) {
  .theme-toggle {
    width: 3.2rem;
  }
}
/* = = = = = = = = = = Footer  = = = = = = = = = = */
.main-footer {
  padding: 5px 15px 5px 15px;
  background-color: var(--main-dim);
  box-shadow: 0 5px 5px -6px var(--shadow) inset;
}

.footer-wrapper {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  align-items: center;
}

.logo-symbol {
  max-width: 45px;
  max-height: 45px;
}

.logo-link {
  display: block;
  color: var(--grey4);
  -webkit-transition: all 350ms cubic-bezier(0.13, 0.43, 0.54, 1.82);
  transition: all 350ms cubic-bezier(0.13, 0.43, 0.54, 1.82);
}
.logo-link:hover {
  transform: scale(1.2);
  opacity: 0.6;
  color: var(--main);
}

.copyright {
  text-align: center;
  color: var(--grey4);
  font-size: 0.6em;
  opacity: 0.75;
}

.footer-nav {
  display: flex;
}
.footer-nav div {
  display: flex;
  padding: 3px 5px;
  flex: 1;
  text-align: center;
  align-items: center;
  justify-content: center;
}
.footer-nav a {
  color: var(--grey4);
  text-decoration: none;
  font-size: 14px;
  font-weight: var(--font2-bold);
  font-family: var(--font2);
}

.footer-wrapper a:hover {
  opacity: 0.6;
}

@media (max-width: 640px) {
  .footer-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .footer-nav {
    font-size: 0.9rem;
  }
  .main-footer {
    padding: 5px;
  }
  .copyright {
    display: none;
  }
}
/*- Font-awesome icons centered within their containers by default -*/
.fa {
  margin: auto;
}

/* = = = = = = = = = = Search Page = = = = = = = = = = */
.search-box {
  padding-top: 40px;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  background: var(--background);
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  -webkit-transform: translate(0, -100%) scale(0, 0);
  -ms-transform: translate(0, -100%) scale(0, 0);
  transform: translate(0, -100%) scale(0, 0);
  max-height: 100vh;
  overflow: scroll;
}

.search-grid {
  width: 80%;
  margin: 0 auto;
}

.search-form {
  margin-bottom: 15px;
}
.search-form .search {
  width: 100%;
  padding: 15px;
  border: none;
  border-bottom: 1px solid var(--grey5);
  outline: none;
  font-size: 21px;
  color: var(--grey1);
  background: var(--background);
}

.results-search {
  list-style: none;
  overflow: scroll;
  margin: 0;
  padding: 0;
  max-height: 80vh;
}
.results-search li {
  padding-bottom: 15px;
  margin-bottom: 15px;
  border-bottom: 1px solid #d9d9d9;
}
.results-search li a {
  font-family: var(--font2);
  text-decoration: none;
  color: var(--accent-dark);
  font-weight: var(--font2-bold);
}
.results-search li a:hover {
  color: var(--accent);
  opacity: 0.8;
}
.results-search > * p {
  font-weight: var(--font1-light);
  color: var(--grey3);
  font-size: 0.75em;
  line-height: 1.3;
}

.search-box.search-active {
  -webkit-transform: translate(0, 0) scale(1, 1);
  -ms-transform: translate(0, 0) scale(1, 1);
  transform: translate(0, 0) scale(1, 1);
}

.icon-close-container {
  position: absolute;
  top: 0;
  right: 0;
}

#results-container {
  background: transparent;
}

/* = = = = = = = = = = Post-Card (homepage) = = = = = = = = = = */
.post-card-box {
  margin: 50px 0 15px;
  padding: 0;
}
.post-card-box li {
  position: relative;
  list-style: none;
  float: left;
  width: 50%;
}

.post-card {
  position: relative;
  margin: 0px 5px 5px 0;
}

.post-card-image {
  display: block;
  width: 100%;
  min-height: 150px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.post-card-image::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.15);
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.post-card:hover .post-card-image::after {
  background: rgba(0, 0, 0, 0);
}
.post-card:hover .post-card-body {
  background-color: rgba(0, 0, 0, 0.7490196078);
}

.post-card-body {
  position: absolute;
  left: 5%;
  bottom: 5%;
  background-color: rgba(0, 0, 0, 0.6);
  padding-left: 5px;
  width: 95%;
}

.post-card-tags {
  font-size: 10px;
}

.tag {
  text-decoration: none;
  text-transform: uppercase;
  color: white;
  white-space: nowrap;
}
.tag:hover {
  text-decoration: underline;
  color: var(--grey4);
}

.post-card-title-and-meta {
  margin: 0;
  color: white;
  line-height: 1.2;
}

.post-card-link {
  text-decoration: none;
  font-family: var(--font2), sans-serif;
  font-weight: var(--font2-bold);
  text-transform: uppercase;
  color: white;
}
.post-card-link:hover {
  opacity: 0.75;
  color: var(--grey4);
}

/* = = = = post's read time and date (meta data) = = = = */
.post-card-meta {
  font-size: 0.6em;
  white-space: nowrap;
  display: block;
  padding-top: 4px;
}
.post-card-meta i {
  display: none;
}

.recent-item-meta {
  padding-left: 5px;
  font-size: 8px;
  display: block;
}
.recent-item-meta p {
  margin-block-start: 0;
  margin-block-end: 4px;
}

