  height: 1px;
  margin-top: 2em;
}

@media (min-width: 64em) {
  .sidebar.side {
    display: block;
    position: -webkit-sticky;
    position: sticky;
    top: 32px;
    width: 115px;
    margin-left: -135px;
    float: left;
  }
  .sidebar.side .tag-cloud {
    display: block;
  }
  ul.tags.side {
    display: block;
  }
